<template>
  <v-col cols="12">
    <v-card><br>
      <h3 for="" class="ml-4">Pinjaman Baru</h3>
      <v-card-title>
        <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details
          width="100px"></v-text-field>
      </v-card-title>
      <v-data-table :headers="headers" :items="pinjamanbaru" loading-text="Loading... Please wait"
        :loading="loading" :search="search">
        <template #[`item.index`]="{ item }">
          {{ pinjamanbaru.indexOf(item) + 1 }}.
        </template>
        <template #[`item.SubmitDate`]="{ item }">
          {{ toDateStringMount(item.SubmitDate) }}
        </template>
      </v-data-table>
    </v-card>
  </v-col>
</template>
<script>
import { mapState } from 'vuex'
import Utils from '@/utils/util'
export default {
  name: 'TablePinjamanBaru',
  data: () => ({
    search: '',
    headers: [
      {
        text: '#',
        align: 'start',
        sortable: false,
        value: 'index'
      },
      { text: 'Borrower', sortable: false, value: 'EmployeeName' },
      { text: 'NIK', sortable: false, value: 'EmployeeID' },
      { text: 'Lokasi', sortable: false, value: 'WorkLocation' },
      { text: 'Date', sortable: false, value: 'SubmitDate' }
    ]
  }),
  computed: {
    ...mapState('dashboard', {
      loading: (state) => state.loading,
      pinjamanbaru: (state) => state.data.pinjamanbaru
    })
  },
  methods: {
    toDateStringMount (date) {
      return Utils.toDateStringMount(date)
    }
  }
}
</script>
